___CSS_LOADER_EXPORT___.push([module.id, `* {
  margin: 0;
  border: 0;
  padding: 0;
  font-family: "Bitter", serif;
}

ion-content {
  --background: linear-gradient(to top, #33cccc 0%, #006666 100%);
}

ion-toolbar {
  --background: none;
  color: #fff;
  margin-top: 20px;
}

ion-icon {
  color: rgb(255, 255, 255);
  margin: 10px;
}

#titulo {
  font-size: 20px;
  color: #fff;
  text-transform: uppercase;
  font-weight: 500;
  text-align: center;
  margin-right: 20px;
}

#container {
  text-align: center;
  position: absolute;
  left: 0;
  right: 0;
  top: 50%;
  transform: translateY(-50%);
}

ion-list {
  --ion-item-background: transparent;
  margin: 30px 20px;
}

ion-list ion-item {
  margin-bottom: 15px;
  --border-color: #004b4d;
}

.legenda {
  color: #fff;
  font-weight: 300;
}

ion-input {
  font-size: 15px;
}

#botao-confirmar {
  padding-left: 20px;
  padding-right: 20px;
  text-transform: uppercase;
  font-weight: 300;
  color: #fff;
  --background: #004b4d;
}

#botao-confirmar:hover {
  color: #000;
  transition: all 300ms ease-in-out;
}

ion-checkbox {
  margin-right: 10px;
  --size: 15px;
}`, "",{"version":3,"sources":["webpack://./src/app/home/components/alterar-dados/alterar-dados.page.scss"],"names":[],"mappings":"AAAA;EACI,SAAA;EACA,SAAA;EACA,UAAA;EACA,4BAAA;AACJ;;AAEA;EACI,+DAAA;AACJ;;AAEA;EACI,kBAAA;EACA,WAAA;EACA,gBAAA;AACJ;;AAEA;EACI,yBAAA;EACA,YAAA;AACJ;;AAEA;EACI,eAAA;EACA,WAAA;EACA,yBAAA;EACA,gBAAA;EACA,kBAAA;EACA,kBAAA;AACJ;;AAEA;EACI,kBAAA;EACA,kBAAA;EACA,OAAA;EACA,QAAA;EACA,QAAA;EACA,2BAAA;AACJ;;AAEA;EACI,kCAAA;EACA,iBAAA;AACJ;;AAEA;EACI,mBAAA;EACA,uBAAA;AACJ;;AAEA;EACI,WAAA;EACA,gBAAA;AACJ;;AAEA;EACI,eAAA;AACJ;;AAEA;EACI,kBAAA;EACA,mBAAA;EACA,yBAAA;EACA,gBAAA;EACA,WAAA;EACA,qBAAA;AACJ;;AAEA;EACI,WAAA;EACA,iCAAA;AACJ;;AAEA;EACI,kBAAA;EACA,YAAA;AACJ","sourcesContent":["* {\r\n    margin: 0;\r\n    border: 0;\r\n    padding: 0;\r\n    font-family: 'Bitter', serif;\r\n}\r\n\r\nion-content {\r\n    --background: linear-gradient(to top, #33cccc 0%, #006666 100%);\r\n}\r\n\r\nion-toolbar {\r\n    --background: none;\r\n    color: #fff;\r\n    margin-top: 20px;\r\n}\r\n\r\nion-icon{\r\n    color: rgb(255, 255, 255);\r\n    margin: 10px;\r\n}\r\n\r\n#titulo {\r\n    font-size: 20px;\r\n    color: #fff;\r\n    text-transform: uppercase;\r\n    font-weight: 500;\r\n    text-align: center; \r\n    margin-right: 20px;\r\n}\r\n\r\n#container {\r\n    text-align: center;\r\n    position: absolute;\r\n    left: 0;\r\n    right: 0;\r\n    top: 50%;\r\n    transform: translateY(-50%);\r\n}\r\n\r\nion-list {\r\n    --ion-item-background: transparent;\r\n    margin: 30px 20px;\r\n}\r\n\r\nion-list ion-item{\r\n    margin-bottom: 15px;\r\n    --border-color: #004b4d;\r\n}\r\n\r\n.legenda {\r\n    color: #fff;\r\n    font-weight: 300;\r\n}\r\n\r\nion-input {\r\n    font-size: 15px;\r\n}\r\n\r\n#botao-confirmar {\r\n    padding-left: 20px; \r\n    padding-right: 20px;\r\n    text-transform: uppercase;\r\n    font-weight: 300;\r\n    color:#fff;\r\n    --background: #004b4d;\r\n}\r\n\r\n#botao-confirmar:hover {\r\n    color: #000;\r\n    transition: all 300ms